@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,500&display=swap);
.navbar{background-color:white}.navbar .navbar-brand{font-size:1.9rem;color:red}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar a:hover{color:cornflowerblue}.navbar .menu_active{font-weight:bold;border-bottom:1px solid red}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0px 0px 10px 0px;font-size:48px;font-weight:700;color:black}#header .brand-name{color:#3498db}#header h2{font-size:24px;color:#484848}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background-color:#3498db;color:white}.header-img{text-align:right}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}

